@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

#root{
  position: relative;
  height: 100vh;
  width: 100vw;
  background-image: url(/tom-and-jerry/static/media/bg4.b28dad91.png);
  background-size: cover;
  background-position-y: bottom;
}

.App{
  position: absolute;
  height: 100%;
  width: 100%;
}

@media (max-width : 1024px){
  #root{
    background-image: linear-gradient(to right, gray, white, gray);
  }
}

.game-container{
    position: absolute;
    top: 50px;
    right: 30px;
    height: 300px;
    width: 60%;
    border: 2px solid black;
    overflow: hidden;
}


/* Jerry */
.jerry{
    position: absolute;
    top: 220px;
    left: 80px;
    height: 80px;
    width: 60px;
    background-image: url(/tom-and-jerry/static/media/jerry.ed40a189.png);
    background-size: cover;
}

.jump{
    -webkit-animation: animate_jerry 1s ease-in-out 0s infinite;
            animation: animate_jerry 1s ease-in-out 0s infinite;
}

@-webkit-keyframes animate_jerry {
    0%{ top: 220px;}
    50%{ top: 20px;}
    100%{ top: 220px;}
}

@keyframes animate_jerry {
    0%{ top: 220px;}
    50%{ top: 20px;}
    100%{ top: 220px;}
}



/* Tom */
.tom{
    position: absolute;
    bottom: 0;
    left: 60vw;
    height: 90px;
    width: 120px;
    /* background-image: url('../../Assets/tom.png'); */
    background-image: url(/tom-and-jerry/static/media/angryRat2.fae9788f.png);
    background-size: cover;
}

.run{
    -webkit-animation: animate_tom 1.8s linear 0s infinite;
            animation: animate_tom 1.8s linear 0s infinite;
}

@-webkit-keyframes animate_tom {
    0%{ left: 60vw}
   
    100%{ left: 0vw;}
}

@keyframes animate_tom {
    0%{ left: 60vw}
   
    100%{ left: 0vw;}
}

/* Game Over */

.game-over{
    position: absolute;
    top: 10%;
    left: 40%;
    font-size: 2rem;
    font-weight: 500;
}

.hide{
    display: none;
}

.show{
    display: block;
}

.score{
    position: absolute;
    top: 1%;
    left: 45%;
    font-size: 1rem;
    font-weight: 900;
}

.message-to-rotate-screen{
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: black;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}



/*=========================================== MOBILE ===========================================*/
